/* Quote */
blockquote.wp-block-quote,
blockquote {
  font-family: "Source Sans Pro", sans-serif;
  padding: 20px 40px;
  margin-top: 80px;
  margin-bottom: 80px;
  border-width: 0;
  position: relative;
  z-index: 0; }
  blockquote.wp-block-quote,
  blockquote.wp-block-quote p,
  blockquote,
  blockquote p {
    font-size: 30px;
    color: #333;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal; }
  blockquote.wp-block-quote cite,
  blockquote cite {
    color: #777777;
    font-family: "Source Sans Pro", sans-serif;
    letter-spacing: normal;
    line-height: 1.5;
    font-weight: 300;
    font-style: italic; }
    blockquote.wp-block-quote cite::before,
    blockquote cite::before {
      content: "-";
      margin-right: 5px; }
  blockquote.wp-block-quote > *:last-child,
  blockquote > *:last-child {
    margin-bottom: 0; }

blockquote.wp-block-quote::before,
blockquote::before {
  background-image: url(../images/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  content: " ";
  width: 50px;
  height: 50px;
  display: block;
  z-index: -1;
  margin-bottom: 40px;
  position: relative;
  font-style: normal;
  text-align: center;
  margin-left: auto;
  margin-right: auto; }

blockquote.wp-block-quote::after,
blockquote::after {
  color: #f5f5f5;
  color: #333;
  opacity: 0.1;
  content: "\201C";
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
  font-size: 140px;
  height: 0;
  left: 0px;
  top: 40px;
  z-index: -1;
  position: absolute;
  font-style: normal; }

